import range from 'lodash/range'
import random from 'lodash/random'

const allGroups = ['group A', 'group B', 'group C', 'group D', 'group E', 'group F']

const generateDataSet = (group, size) => {
    const values = range(size).map(() => random(0, 500))
    values.sort()

    return values.map((price, index) => ({
        id: `${group}.${index}`,
        group,
        price,
        volume: random(4, 20),
    }))
}

export const generateLightDataSet = () => {
    const groups = allGroups.slice(0, 3)
    const data = groups.reduce((acc, group) => {
        return [...acc, ...generateDataSet(group, 60 + Math.round(Math.random() * 40))]
    }, [])

    return { groups, data }
}

export const generateHeavyDataSet = () => {
    const data = allGroups.reduce((acc, group) => {
        return [...acc, ...generateDataSet(group, 80 + Math.round(Math.random() * 120))]
    }, [])

    return { groups: allGroups, data }
}
